//引入pageHeader作为组件
import pageHeader from './pageHeader.vue';
//接口引进来
import interfaces from '../../../utils/interfaces.js'
export default{
	data(){
		return {
			swiperList:[],//这里用来存储轮播数组
			categoryList:[],//分类
			promotion:[],//活动
			currentSwiper:0
		}
	},
	components:{
		pageHeader
	},
	onLoad() {
		//初始化执行下拿取数据
		this.initData();
	},
	methods:{
		initData(){
			this.request({
			    url: interfaces.getMallData,
				//这里有个存疑，https中的success回调函数有点不明白
			    success: ((res) => {
					// console.log(res);
			        this.swiperList=res.data.swiperList;
					this.categoryList=res.data.categoryList;
					this.promotion=res.data.promotion;
			    })
			});
		},
		//监听swipe的事件
		swiperChange(event){
			// console.log(event.detail.current);
			this.currentSwiper = event.detail.current;
		},
		//获取到点击的分类的值
		handleCategory(item){
			uni.navigateTo({
				url:"../../goods/goodsList?name="+item.name
			})
		},
		handlePromotion(item){
			uni.showToast({
				title:item.title,
				icon:"none"
			})
		}
	}
}
